<template>
  <div class="detail">
    <!-- 头部 -->
    <van-nav-bar
      title="商品详情"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />

    <!-- 中部 -->
    <div class="product-box">
      <div>
        <img :src="productDetailData.large_img" class="auto-img" />
      </div>

      <div class="box">
        <!-- 爱心 -->
        <ul class="pro-box">
          <li class="pro-name">{{ productDetailData.name }}</li>
        </ul>

        <!-- 商品规格 -->
        <div class="pro-rule">
          <div class="rule-item" v-for="(item, index) in rules" :key="index">
            <span class="fl rule-title">{{ item.title }}</span>
            <ul class="fl rule-list">
              <li
                v-for="(value, i) in item.rule"
                :key="i"
                :class="{ active: value.isActive }"
                @click="toggleRule(value, index)"
              >
                {{ value.text }}
              </li>
            </ul>
          </div>
        </div>

        <!-- 商品描述 -->
        <div class="pro-desc">
          <div class="desc-title">商品描述</div>
          <div class="desc-info">
            <p v-for="(item, index) in desc" :key="index">{{ item }}</p>
          </div>
        </div>

        <!-- 价格数量 -->
        <div class="pro-price-count">
          <div class="price">￥{{ productDetailData.price }}</div>
          <div class="count clearfix">
            <span class="icon fl left" @click="decrease"></span>
            <input class="fl pro-count" type="text" v-model="count" readonly />
            <span class="icon fl right" @click="inscease"></span>
          </div>
        </div>
      </div>
    </div>

    <!-- 底部 -->
    <van-goods-action>
      <van-goods-action-icon
        icon="cart-o"
        text="购物车"
        :badge="
          shopcartTotal == 0 ? '' : shopcartTotal >= 100 ? '99+' : shopcartTotal
        "
        @click="goPage"
      />
      <van-goods-action-icon
        :icon="actionIcon.icon"
        :text="actionIcon.text"
        :color="actionIcon.color"
        @click="like"
      />
      <van-goods-action-button
        color="#5da7f7"
        type="warning"
        text="加入购物车"
        @click="addShopcart"
      />
      <van-goods-action-button
        color="#1989fa"
        type="danger"
        text="立即购买"
        @click="buy"
      />
    </van-goods-action>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { utils } from "../utils/utils";
export default {
  name: "Detail",

  computed: {
    ...mapState("detailModule", [
      "productDetailData",
      "rules",
      "desc",
      "count",
      "isLike",
      "shopcartTotal",
      "actionIcon",
      "isShow",
    ]),
  },

  created() {
    //加载提示
    this.$toast.loading({
      //禁止背景点击
      forbidClick: true,
      //展示时长为0禁止自动关闭
      duration: 0,
      //内容
      message: "正在加载...",
    });

    //置空商品详情数据
    this.$store.commit("detailModule/emptyData");

    //   截取pid路由参数，商品详情pid
    let pid = this.$route.params.pid;

    this.axios({
      method: "GET",
      url: "/productDetail",
      params: {
        appkey: this.appkey,
        pid,
      },
    })
      .then((d) => {
        if (d.data.code == 600) {
          this.$store.commit(
            "detailModule/changeProductDetailData",
            d.data.result[0]
          );

          //查看当前收藏商品
          this.findlike();

          //获取购物车商品总数量
          this.shopcartRows();

          //关闭加载提示
          this.$toast.clear();
        }
      })
      .catch((err) => {
        //关闭加载提示
        this.$toast.clear();
      });
  },

  methods: {
    //增加数量
    inscease() {
      let count = this.count + 1;
      this.$store.commit("detailModule/changeData", {
        key: "count",
        value: count,
      });
    },

    //减少数量
    decrease() {
      if (this.count == 1) {
        return;
      }
      let count = this.count - 1;
      this.$store.commit("detailModule/changeData", {
        key: "count",
        value: count,
      });
    },

    //切换商品规格
    toggleRule(item, index) {
      if (item.isActive) {
        return;
      }
      let cuurentRule = this.rules[index];
      for (let i = 0; i < cuurentRule.rule.length; i++) {
        if (cuurentRule.rule[i].isActive) {
          cuurentRule.rule[i].isActive = false;
          break;
        }
      }
      item.isActive = true;
    },

    //查看当前商品是否被收藏
    findlike() {
      let tokenString = localStorage.getItem("_t");

      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "正在加载...",
      });

      this.axios({
        method: "GET",
        url: "/findlike",
        params: {
          appkey: this.appkey,
          pid: this.productDetailData.pid,
          tokenString,
        },
      })
        .then((d) => {
          //关闭加载提示
          this.$toast.clear();
          if (d.data.code == 1000) {
            //修改islike
            this.$store.commit("detailModule/changeData", {
              key: "isLike",
              value: d.data.result.length > 0,
            });
            //修改爱心
            if (this.isLike) {
              this.$store.commit("detailModule/changeData", {
                key: "actionIcon",
                value: {
                  icon: "like",
                  text: "已收藏",
                  color: "#EE0A24",
                },
              });
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //收藏商品。取消收藏
    like() {
      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "加载中...",
      });
      let tokenString = localStorage.getItem("_t");

      let url = this.isLike ? "/notlike" : "/like";
      this.axios({
        method: "POST",
        url,
        data: {
          appkey: this.appkey,
          pid: this.productDetailData.pid,
          tokenString,
        },
        transformRequest: utils.transformRequest,
      })
        .then((d) => {
          //关闭加载提示
          this.$toast.clear();
          //判断是否有登陆
          if (d.data.code == 700) {
            this.noTokenLogin();
            return;
          }
          //加载提示
          this.$toast({
            message: d.data.msg,
          });
          //修改爱心
          if (!this.isLike) {
            this.$store.commit("detailModule/changeData", {
              key: "actionIcon",
              value: {
                icon: "like",
                text: "已收藏",
                color: "#EE0A24",
              },
            });
          } else {
            this.$store.commit("detailModule/changeData", {
              key: "actionIcon",
              value: {
                icon: "like",
                text: "收藏",
                color: "#6A6A6B",
              },
            });
          }
          //修改isLike
          this.$store.commit("detailModule/changeData", {
            key: "isLike",
            value: !this.isLike,
          });
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //加入购物车
    addShopcart(type) {
      let tokenString = localStorage.getItem("_t");

      //获取商品规格
      let ruleData = [];
      this.rules.forEach((v) => {
        for (let i = 0; i < v.rule.length; i++) {
          if (v.rule[i].isActive) {
            ruleData.push(v.rule[i].text);
            break;
          }
        }
      });
      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "加载中...",
      });
      this.axios({
        method: "POST",
        url: "/addShopcart",
        data: {
          appkey: this.appkey,
          pid: this.productDetailData.pid,
          count: this.count,
          rule: ruleData.join("/"),
          tokenString,
        },
        transformRequest: utils.transformRequest,
      })
        .then((d) => {
          this.$toast.clear();

          //判断是否有登陆
          if (d.data.code == 700) {
            this.noTokenLogin();
            return;
          }
          //加载提示
          this.$toast({
            message: d.data.msg,
          });
          if (d.data.code == 3000) {
            //修改购物车显示数量
            if (d.data.status == 1) {
              this.$store.commit("detailModule/changeData", {
                key: "shopcartTotal",
                value: this.shopcartTotal + 1,
              });
            }
            //立即购买，跳转到结算页面，携带sid
            if (type == "buy") {
              this.$router.push({ name: "Pay", query: { sids: d.data.sid } });
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //立即购买
    buy() {
      this.addShopcart("buy");
    },

    //获取购物车商品总数量
    shopcartRows() {
      let tokenString = localStorage.getItem("_t");
      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "加载中...",
      });
      this.axios({
        method: "GET",
        url: "/shopcartRows",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((d) => {
          this.$toast.clear();
          if (d.data.code == 8000) {
            this.$store.commit("detailModule/changeData", {
              key: "shopcartTotal",
              value: d.data.result,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //返回上一级
    back() {
      this.$router.go(-1);
    },

    //跳转
    goPage() {
      this.$router.push({ name: "Shopcart" });
    },

    //游客登陆
    noTokenLogin() {
      this.$dialog
        .confirm({
          title: "前往登陆",
          message: "您还没有登陆，请问您要前往登陆界面吗",
        })
        .then((text) => {
          if (text == "confirm") {
            this.$router.push({ name: "Login" });
          }
        })
        .catch(() => {});
    },
  },
};
</script>

<style lang="less" scoped>
.detail {
  padding-bottom: 60px;

  //中部
  .product-box {
    background-color: #fff;
    padding-top: 46px;

    .box {
      padding: 0 10px;

      // <!-- 爱心 -->
      .pro-box {
        display: flex;
        .pro-name {
          margin-top: 10px;
          font-size: 16px;
          color: #1296db;
          line-height: 24px;
        }
      }

      // <!-- 商品规格 -->
      .pro-rule {
        margin-top: 20px;
        border-bottom: 1px solid #e9e9e9;
        margin-bottom: 1px;
        .rule-item {
          height: 30px;
          padding: 10px 0;
          .rule-list > li {
            float: left;
            width: 70px;
            height: 30px;
            line-height: 30px;
            color: #666;
            background-color: #e5e5e5;
            margin-right: 20px;
            border-radius: 20px;
            text-align: center;
            font-size: 14px;
            &.active {
              background-color: #5da7f7;
              color: #fff;
            }
          }
          .rule-title {
            line-height: 30px;
            width: 60px;
            font-size: 16px;
          }
        }
      }

      // <!-- 商品描述 -->
      .pro-desc {
        margin-top: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid #e9e9e9;
        .desc-title {
          color: #444;
          font-size: 16px;
        }
        .desc-info {
          color: #666;
          font-size: 14px;
        }
        > p {
          margin: 10px 0 0;
        }
      }

      // <!-- 价格数量 -->
      .pro-price-count {
        display: flex;
        height: 50px;

        .price {
          color: #1985fa;
          font-size: 16px;
          line-height: 50px;
        }

        .count {
          margin-top: 13px;
          font-size: 14px;
          margin-left: auto;

          .icon {
            width: 24px;
            height: 24px;
            border-radius: 50%;
            background-position: center center;
            background-size: cover;
            background-repeat: no-repeat;

            &.left {
              background-image: url("../assets/edd.png");
            }
            &.right {
              background-image: url("../assets/add.png");
            }
          }

          .pro-count {
            height: 24px;
            width: 30px;
            margin: 0 5px;
            text-align: center;
          }
        }
      }
    }
  }
}
</style>